  }
}

.breadcrumbs {
  height: 100%;
  margin: auto;
  max-width: 960px;
  padding: 0 20px;
  position: relative;
  width: 100%;
}

@media screen and (min-width: 980px), print {
  .breadcrumbs {
    padding: 0 15px;
  }
}

@media screen and (min-width: 1200px), print {
  .breadcrumbs {
    max-width: 1160px;
  }
}

.breadcrumbs ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.breadcrumbs li {
  display: inline-block;
  vertical-align: top;
}

.breadcrumbs li a {
  text-decoration: none;
}

.breadcrumbs li a:hover, .breadcrumbs li a:focus {
  text-decoration: underline;
}

.breadcrumbs li:after {
  content: "\e902";
  margin: 0 10px;
  position: relative;
  top: 3px;
}

.breadcrumbs li:last-child {
  font-weight: bold;
}

.breadcrumbs li:last-child:after {
  content: "";
}

.form-item {
  margin-bottom: 15px;
}

.form-item input[type="text"],
.form-item input[type="email"],
.form-item input[type="password"],
.form-item textarea {
  transition-property: border-bottom, color, outline;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  border: none;
  border-bottom: 1px solid #8c8c8c;
  font-size: 1em;
  font-weight: 300;
  padding: 10px 0;
  width: 100%;
}

.form-item input[type="text"]:focus,
.form-item input[type="email"]:focus,
.form-item input[type="password"]:focus,
.form-item textarea:focus {
  border-bottom: 1px solid #000000;
  color: #000000;
}

.form-item input[type="text"]:disabled,
.form-item input[type="email"]:disabled,
.form-item input[type="password"]:disabled,
.form-item textarea:disabled {
  background: none;
  border-bottom: 1px dotted #8c8c8c;
}

.form-item input[type="text"]:disabled:hover, .form-item input[type="text"]:disabled:focus,
.form-item input[type="email"]:disabled:hover,
.form-item input[type="email"]:disabled:focus,
.form-item input[type="password"]:disabled:hover,
.form-item input[type="password"]:disabled:focus,
.form-item textarea:disabled:hover,
.form-item textarea:disabled:focus {
  cursor: not-allowed;
}

.form-item input[type="radio"],
.form-item input[type="checkbox"] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.form-item input[type="radio"]:focus + label:before,
.form-item input[type="checkbox"]:focus + label:before {
  border: 2px solid #000000;
}

.form-item input[type="radio"]:checked + label:before,
.form-item input[type="checkbox"]:checked + label:before {
  border: 2px solid #000000;
}

.form-item.-radio-button label, .form-item.-checkbox label {
  cursor: pointer;
  display: block;
  font-size: 1em;
  line-height: 1.3;
  padding-left: 30px;
  position: relative;
  margin-bottom: 10px;
  width: 100%;
}

.form-item.-radio-button label:before, .form-item.-checkbox label:before {
  transition-property: border, background;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  border: 2px solid #6f6f6f;
  content: "";
  display: inline-block;
  height: 18px;
  left: 0;
  position: absolute;
  width: 18px;
}

.form-item.-radio-button label:after, .form-item.-checkbox label:after {
  transition-property: border, top, transform;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  height: 18px;
  position: absolute;
  margin: auto;
  transform: scale(0);
  width: 18px;
}

.form-item.-radio-button label:before {
  border-radius: 50%;
  margin-right: 18px;
  position: absolute;
}

.form-item.-radio-button label:after {
  background: transparent;
  border-radius: 50%;
  content: "";
  display: block;
  left: 0;
  top: 0;
}

.form-item.-radio-button input[type="radio"]:checked + label:after {
  background: #000000;
  transform: scale(0.45);
}

.form-item.-checkbox label:before {
  border-radius: 2px;
}

.form-item.-checkbox label:after {
  color: transparent;
  content: "\e28a";
  display: inline-block;
  height: 10px;
  left: 1px;
  top: 6px;
  width: 10px;
}

.form-item.-checkbox input[type="checkbox"]:checked + label:before {
  background: #000000;
}

.form-item.-checkbox input[type="checkbox"]:checked + label:after {
  color: #ffffff;
  top: 0;
  transform: scale(1);
}

#navigation {
  transition-property: height;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  zoom: 1;
  /* For IE 6/7 (trigger hasLayout) */
  height: 45px;
  position: absolute;
  right: 0;
  top: 45px;
  width: 100%;
  z-index: 5;
}

#navigation:before, #navigation:after {
  content: '';
  display: table;
}

#navigation:after {
  clear: both;
}

@media screen and (min-width: 980px), print {
  #navigation {
    background: #262626;
    height: auto;
    position: static;
    width: 100%;
  }
  #navigation .sub-nav,
  #navigation .lvl2,
  #navigation .lvl3 {
    display: none;
  }
}

#navigation.active {
  z-index: 25;
}

#navigation ol {
  zoom: 1;
  /* For IE 6/7 (trigger hasLayout) */
  background: #ffffff;
  margin: 0 20px;
  margin-bottom: 0;
  overflow: hidden;
  padding: 0;
}

#navigation ol:before, #navigation ol:after {
  content: '';
  display: table;
}

#navigation ol:after {
  clear: both;
}

#navigation ol li {
  transition-property: all;
  transition-duration: 0.75s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  opacity: 0;
  overflow: hidden;
  position: relative;
  transform: translateY(-30px);
  will-change: transform;
  vertical-align: top;
}

#navigation ol li:nth-child(1) {
  transition-delay: 0.1s, 0.1s;
}

#navigation ol li:nth-child(2) {
  transition-delay: 0.2s, 0.2s;
}

#navigation ol li:nth-child(3) {
  transition-delay: 0.3s, 0.3s;
}

#navigation ol li:nth-child(4) {
  transition-delay: 0.4s, 0.4s;
}

#navigation ol li:nth-child(5) {
  transition-delay: 0.5s, 0.5s;
}

#navigation ol li:nth-child(6) {
  transition-delay: 0.6s, 0.6s;
}

#navigation ol li:nth-child(7) {
  transition-delay: 0.7s, 0.7s;
}

#navigation ol li:nth-child(8) {
  transition-delay: 0.8s, 0.8s;
}

#navigation ol li:nth-child(9) {
  transition-delay: 0.9s, 0.9s;
}

#navigation ol li:nth-child(10) {
  transition-delay: 1s, 1s;
}

@media screen and (min-width: 980px), print {
  #navigation ol li {
    transform: none;
    opacity: 1;
  }
}

#navigation ol a {
  color: #000000;
  display: inline-block;
  margin-bottom: 0;
  overflow: hidden;
  padding: 10px;
  position: relative;
  text-decoration: none;
  vertical-align: top;
  width: 100%;
}

#navigation ol a.active {
  background: #333333;
}

#navigation ol a span {
  position: relative;
  z-index: 5;
}

@media screen and (min-width: 980px), print {
  #navigation ol a {
    color: #ffffff;
  }
}

#navigation .nav {
  zoom: 1;
  /* For IE 6/7 (trigger hasLayout) */
  height: 0;
  width: 100%;
}

#navigation .nav:before, #navigation .nav:after {
  content: '';
  display: table;
}

#navigation .nav:after {
  clear: both;
}

#navigation .nav:after {
  transition-property: opacity;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  background: rgba(0, 0, 0, 0.5);
  content: '';
  height: inherit;
  left: 0;
  opacity: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 5;
}

#navigation .nav.active {
  height: 100%;
}

#navigation .nav.active:after {
  opacity: 1;
}

@media screen and (min-width: 980px), print {
  #navigation .nav {
    height: auto;
    margin: auto;
    max-width: 960px;
  }
}

@media screen and (min-width: 1200px), print {
  #navigation .nav {
    max-width: 1160px;
  }
}

#navigation .lvl2,
#navigation .lvl3 {
  display: none;
  margin: 0;
}

#navigation .lvl2.active > li,
#navigation .lvl3.active > li {
  transform: none;
  opacity: 1;
}

#navigation .lvl2 a,
#navigation .lvl3 a {
  border-bottom: 0;
  width: 100%;
}

#navigation .lvl2 .sub-nav.icon-arrow:before, #navigation .lvl2 .sub-nav.icon-arrow:after,
#navigation .lvl3 .sub-nav.icon-arrow:before,
#navigation .lvl3 .sub-nav.icon-arrow:after {
  background: #ffffff;
}

#navigation .lvl1 {
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  list-style-type: none;
  margin: 0;
  padding: 0;
  float: right;
  list-style: none;
  max-width: 300px;
  overflow: auto;
  position: relative;
  top: 0;
  transform: translateX(100%);
  will-change: transform;
  width: 100%;
  z-index: 10;
}

@media screen and (min-width: 980px), print {
  #navigation .lvl1 {
    transform: none;
    float: none;
    max-width: none;
  }
}

#navigation .lvl1.active {
  box-shadow: 0 6px 6px -3px rgba(0, 0, 0, 0.2), 0 10px 14px 1px rgba(0, 0, 0, 0.14), 0 4px 18px 3px rgba(0, 0, 0, 0.12);
  transform: none;
}

#navigation .lvl1.active > li {
  transform: none;
  opacity: 1;
}

#navigation .lvl1 > li {
  border-bottom: 1px solid #9e9e9e;
}

@media screen and (min-width: 980px), print {
  #navigation .lvl1 > li {
    border-bottom: 0;
    overflow: visible;
  }
}

#navigation .lvl1 a {
  padding: 10px 20px;
}

#navigation .lvl1 a.active {
  background: #333333;
}

@media screen and (min-width: 980px), print {
  #navigation .lvl1 {
    background: none;
    height: auto !important;
    overflow: visible;
  }
  #navigation .lvl1:first-child > a {
    padding-left: 0;
  }
  #navigation .lvl1 li {
    border-top: 0;
    float: left;
  }
}

#navigation .lvl2 {
  background: #4d4c4c;
}

#navigation .lvl2 a {
  color: #ffffff;
  padding: 12px 20px 12px 30px;
}

@media screen and (min-width: 980px), print {
  #navigation .lvl2 {
    position: absolute;
    width: 200px;
  }
}

#navigation .lvl3 {
  background: #333333;
}

#navigation .lvl3 li {
  border-top: 1px solid #666666;
}

#navigation .lvl3 a {
  color: #ffffff;
  padding: 12px 20px 12px 40px;
}

.table-wrap {
  margin-bottom: 20px;
  overflow: hidden;
  padding: 6px 0;
  position: relative;
  width: 100%;
}

.table-wrap ::-webkit-scrollbar {
  height: 8px;
  width: 4px;
}

.table-wrap ::-webkit-scrollbar-thumb {
  background: #9e9e9e;
}

.table-wrap:before, .table-wrap:after {
  transition-property: opacity;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  content: '';
  height: 100%;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 50px;
}

.table-wrap:before {
  border-radius: 0 10px 10px 0 / 0 50% 50% 0;
  box-shadow: 4px 0 10px black;
  right: 100%;
}

.table-wrap:after {
  border-radius: 10px 0 0 10px / 50% 0 0 50%;
  box-shadow: -4px 0 10px black;
  left: 100%;
}

.table-wrap.left, .table-wrap.right {
  transition-property: border;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.table-wrap.left {
  border-left: 1px solid #f4f4f4;
}

.table-wrap.left:before {
  opacity: 0.25;
}

.table-wrap.right {
  border-right: 1px solid #f4f4f4;
}

.table-wrap.right:after {
  opacity: 0.25;
}

.table-wrap.-clean:before, .table-wrap.-clean:after {
  display: none;
}

.table-wrap.-clean.right {
  border-right: 0;
  padding: 0;
}

.table-wrap.-clean .table-responsive {
  border: 0;
  overflow: visible;
}

.table-responsive {
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  border: 1px solid #dadada;
  margin-bottom: 15px;
  overflow-y: hidden;
  overflow-x: scroll;
  width: 100%;
}

.table-responsive table {
  margin-bottom: 0;
}

.btn-print-table {
  transition-property: position;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  font-size: 1.6em;
  max-width: 200px;
  padding: 10px;
  width: 100%;
}

@media screen and (min-width: 980px), print {
  .btn-print-table {
    display: none;
  }
}
